import React, { useState } from 'react';
import { Form, Select, InputNumber, Card, Row, Col, Divider, Typography } from 'antd';
import { 
  EnvironmentOutlined, 
  UserOutlined, 
  BranchesOutlined, 
  ColumnHeightOutlined, 
  DashboardOutlined 
} from '@ant-design/icons';
import { UserInputs, BoardRecommendation, RidingStyle } from '../types/snowboard';

const { Title, Text } = Typography;

const SnowboardSelector: React.FC = () => {
  const [form] = Form.useForm();
  const [recommendation, setRecommendation] = useState<BoardRecommendation | null>(null);

  // 
  const calculateLengthRange = (inputs: UserInputs): [number, number] => {
    const { region, gender, height, weight } = inputs;
    
    if (region === 'japan') {
      // 
      const baseLength = height * 0.9;
      return gender === 'male' 
        ? [baseLength - 5, baseLength + 5]
        : [baseLength - 10, baseLength];
    } else {
      // 
      // 2
      const baseLength = (weight * 2.5 + 110) / 2;
      return gender === 'male'
        ? [baseLength - 5, baseLength + 5]
        : [baseLength - 10, baseLength];
    }
  };

  // 
  const getBoardProfile = (style: RidingStyle): string => {
    const profiles: Record<RidingStyle, string> = {
      'all-mountain': 'Hybrid ()',
      'freestyle': 'Rocker ()  Flat ()',
      'carving': 'Camber ()'
    };
    return profiles[style];
  };

  // 
  const getFlexRange = (style: RidingStyle): [number, number] => {
    const flexRanges: Record<RidingStyle, [number, number]> = {
      'all-mountain': [5, 7],
      'freestyle': [1, 5],
      'carving': [6, 10]
    };
    return flexRanges[style];
  };

  const onValuesChange = () => {
    const values = form.getFieldsValue();
    if (values.region && values.gender && values.height && values.weight && values.ridingStyle) {
      const lengthRange = calculateLengthRange(values);
      const [flexMin, flexMax] = getFlexRange(values.ridingStyle);
      
      // 
      const avgLength = (lengthRange[0] + lengthRange[1]) / 2;
      const avgFlex = (flexMin + flexMax) / 2;
      
      setRecommendation({
        lengthRange: {
          min: lengthRange[0],
          max: lengthRange[1],
          avg: avgLength
        },
        profile: getBoardProfile(values.ridingStyle),
        flex: {
          min: flexMin,
          max: flexMax,
          avg: avgFlex
        }
      });
    }
  };

  return (
    <Row justify="center">
      <Col xs={24} sm={20} md={16} lg={12}>
        <Card
          title={
            <div style={{ textAlign: 'center' }}>
              <span role="img" aria-label="snowboarder" style={{ fontSize: '24px', marginRight: '8px' }}></span>
              <Title level={3} style={{ margin: 0, display: 'inline' }}></Title>
              <span role="img" aria-label="snowy mountain" style={{ fontSize: '24px', marginLeft: '8px' }}></span>
            </div>
          }
          style={{ boxShadow: '0 4px 12px rgba(0,0,0,0.1)' }}
        >
          <Form
            form={form}
            layout="vertical"
            onValuesChange={onValuesChange}
          >
            <Form.Item
              name="region"
              label={<><EnvironmentOutlined /> </>}
              rules={[{ required: true, message: '' }]}
            >
              <Select>
                <Select.Option value="japan"></Select.Option>
                <Select.Option value="taiwan"></Select.Option>
              </Select>
            </Form.Item>

            <Form.Item
              name="gender"
              label={<><UserOutlined /> </>}
              rules={[{ required: true, message: '' }]}
            >
              <Select>
                <Select.Option value="male"></Select.Option>
                <Select.Option value="female"></Select.Option>
              </Select>
            </Form.Item>

            <Form.Item
              name="ridingStyle"
              label={<><BranchesOutlined /> </>}
              rules={[{ required: true, message: '' }]}
            >
              <Select>
                <Select.Option value="all-mountain"> (All-Mountain)</Select.Option>
                <Select.Option value="freestyle"> (Freestyle)</Select.Option>
                <Select.Option value="carving"> (Carving)</Select.Option>
              </Select>
            </Form.Item>

            <Form.Item
              name="height"
              label={<><ColumnHeightOutlined />  (cm)</>}
              rules={[
                { required: true, message: '' },
                { type: 'number', min: 140, max: 200, message: '140-200cm' }
              ]}
            >
              <InputNumber style={{ width: '100%' }} />
            </Form.Item>

            <Form.Item
              name="weight"
              label={<><DashboardOutlined />  (kg)</>}
              rules={[
                { required: true, message: '' },
                { type: 'number', min: 40, max: 120, message: '40-120kg' }
              ]}
            >
              <InputNumber style={{ width: '100%' }} />
            </Form.Item>
          </Form>

          {recommendation && (
            <>
              <Divider>
                <span role="img" aria-label="snowflake" style={{ fontSize: '20px' }}></span>  <span role="img" aria-label="snowflake" style={{ fontSize: '20px' }}></span>
              </Divider>
              <Card 
                type="inner" 
                style={{ 
                  background: 'linear-gradient(135deg, #e6f7ff 0%, #ffffff 100%)',
                  border: '1px solid #91d5ff'
                }}
              >
                <Row gutter={[16, 16]}>
                  <Col span={24}>
                    <Text strong></Text>
                    <Text>{recommendation.lengthRange.min.toFixed(1)} - {recommendation.lengthRange.max.toFixed(1)} cm</Text>
                    <Text type="secondary">{recommendation.lengthRange.avg.toFixed(1)} cm</Text>
                  </Col>
                  <Col span={24}>
                    <Text strong></Text>
                    <Text>{recommendation.profile}</Text>
                  </Col>
                  <Col span={24}>
                    <Text strong></Text>
                    <Text>{recommendation.flex.min} - {recommendation.flex.max}</Text>
                    <Text type="secondary">{recommendation.flex.avg.toFixed(1)}</Text>
                  </Col>
                  <Col span={24}>
                    <Text type="secondary">
                      <span role="img" aria-label="lightbulb" style={{ marginRight: '4px' }}></span>
                      5cm
                    </Text>
                  </Col>
                </Row>
              </Card>
            </>
          )}
        </Card>
        <div style={{ textAlign: 'center', margin: '20px 0' }}>
          <Text type="secondary">
            <span role="img" aria-label="ski" style={{ marginRight: '4px' }}></span>
            
          </Text>
        </div>
      </Col>
    </Row>
  );
};

export default SnowboardSelector; 